header > .container {padding: 23px 20px;
display: flex;
flex-direction: row;
z-index: 3;
justify-content: center}

.header__logo {width: 101px;
@include tablet {width: 125px;}
@include desktop {width: 192px;
margin-right: 258px;}}



.header-button__phone {display: flex;
    margin-left: 45px;
    background-color: transparent;
    border: none;
    color: #008CEF;
    @include tablet {display: none;}
}
.header-button__lang {background-color: transparent;
    border: none;
    margin-left: 24px;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.01em;
    color: #BDBDBD;
    @include tablet {margin-left: 100px;margin-right: 38px;};
    @include desktop {margin-left: 130px;margin-right: 118px;}

}

.menu-open {display: flex;
    border: none; background-color: transparent;
    margin-left: 23px;
    color: #487996;
@include tablet {margin-left: 211px;};
@include desktop {display: none;}
}


.header-phone {display: none;
@include tablet {display: flex;flex-direction: column; gap: 10px;};
@include desktop {width: 171px;}}
.icon-call {margin-right: 4px;}

.s {display: flex;}
.drop-down {display: flex;}
.menu-desktop {display: none;
 @include desktop {display: flex;}}
.polygon-icon {color:#008CEF;margin-left: 4px;}


.menu-desktop-list {display: flex; flex-direction: row;gap: 56px;}

.menu-desktop-list__item {font-family: 'Caviar Dreams';
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    letter-spacing: 0.01em; 
    color: #2B2B2B;
    }

.mob-menu {position: fixed;
    z-index: 8;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: #ffffff;
    padding: 20px;
    margin-top: 90px;
    >.container {display: flex;
    flex-direction: column;
  height: 100%;
  align-content: center;
  overflow: auto;}
  }
  .mob-menu__link {font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    
    color: #487996;}
.menu-close {border: none;}